hr {
  width: 100%;
}

strong {
  color: blue;
  font-size: 1rem;
}
.singleMother {
  height: 100%;

  .singleMain {
    display: flex;
    flex-direction: column;
    padding: 50px;
    padding-bottom: 0;
    position: relative;
    min-height: 100%;
    padding-bottom: 100px;

    .titleTextSection {
      .singleTitle {
        text-align: start;
        margin-bottom: 10px;
        max-width: 800px;
        background: lightgrey;
        padding: 5px 5px 5px 8px;
        border-radius: 3px;
      }
      .singleTitle.active {
        border-left: 10px solid green;
      }

      .singleText {
        max-width: 800px;
        text-align: start;
        margin-bottom: 10px;
        white-space: pre-wrap;
      }
    }

    .descSection {
      display: flex;
      justify-content: flex-end;
      margin-top: 10px;
      position: relative;
      padding-bottom: 100px;

      .deleteDiv {
        display: flex;
        align-items: flex-start;
        position: absolute;
        top: 0;
        left: 0;
        .deleteBtn {
          border: none;
          color: red;
          font-size: 0.8rem;
          margin-right: 70px;
          align-self: top;
        }
      }
      .descDiv {
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        align-self: flex-end;
        padding: 10px;
        background: lightblue;
        .dateTime {
          display: flex;
          flex-direction: row;
          .singleDate {
            &:before {
              content: "\00a0 on \00a0";
            }
          }
          .singleTime {
            &:before {
              content: "At\00a0";
            }
          }
        }
      }
      .solvedDiv {
        background: green;
        height: 50px;
        width: 211px;
        margin-top: 10px;
        color: #ffffff;
        font-size: 1.2rem;
        display: flex;
        justify-content: center;
        align-items: center;

        p {
          margin: 0;
        }
      }

      .singleNickname {
        font-weight: 700;
        &:before {
          content: "Asked by ";
          font-weight: 400;
        }
      }
      .singleUpvotes {
        // text-align: center;
      }
      .singleDownvotes {
        // text-align: center;
      }

      @media (max-width: 600px) {
        display: grid;
        grid-template-columns: 100%;
        grid-template-rows: auto auto;
        padding: 0;
        .deleteDiv {
          grid-row: 2;
          position: relative;
          align-self: center;
          width: 100%;
          justify-content: center;
          // background: red;

          .deleteBtn {
            // color: #ffffff;
            padding: 5px 0;
            width: 100%;
            margin: 0;
            font-size: 1.2rem;
            font-weight: 600;
          }
        }
        .descDiv {
          grid-row: 1;
          position: relative;
          width: 100%;
          justify-content: center;
          align-items: center;
          padding: 10px 0px;
          margin-bottom: 10px;
        }
      }
    }

    .commentSection {
      width: 100%;
      margin-top: 30px;
      .commentsHead {
        margin-bottom: 10px;
      }
      .commentForm {
        display: flex;
        flex-direction: column;
        max-width: 500px;
        margin-bottom: 40px;
        .commentInput {
          font-size: 1rem;
          height: 125px;
          margin-bottom: 10px;
          padding: 10px;
          resize: none;
          white-space: pre-wrap;
        }
        .commentSubmit {
          width: 100px;
          font-size: 1rem;
          font-weight: 600;
          padding: 5px 8px;
          background: lightblue;
          cursor: pointer;
          -webkit-appearance: none;
          border: none;
        }
      }

      .commentList {
        display: flex;
        flex-direction: column;
        .commItem {
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          margin: 20px 0;
          
          .commContainer {
            display: flex;
            flex-direction: column;
            border: 1px #333 solid;
            border-radius: 5px;
            
            padding: 15px;
            background: rgb(228, 228, 228);
            z-index: 2;
            .commText {
              white-space: pre-wrap;
            }

            .dateTimeNameDiv {
              display: flex;
              flex-direction: row;
              align-self: flex-end;
              padding-top: 10px;

              .replyBtn {
                border: none;
                appearance: none;
                background: lightblue;
                color: black;
                font-weight: 600;
                cursor: pointer;
              }

              .solvedBtn {
                border: none;
                appearance: none;
                background: green;
                color: white;
                font-weight: 600;
                cursor: pointer;
                margin-left: 14px;
              }
              .deleteCommBtn {
                border: none;
                appearance: none;
                background: red;
                color: black;
                font-weight: 600;
                cursor: pointer;
                margin-left: 14px;
              }
              .commDate {
                &:before {
                  color: blue;
                  content: "\00a0 | \00a0";
                }
              }
              .commTime {
                &:before {
                  color: blue;
                  content: "\00a0 | \00a0";
                }
              }
            }
          }

          .repliesDiv {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            align-self: flex-end; 
            padding: 4px;
            width: 90%;
            z-index: 1;
            background: lightgrey;

            .repItem {
              width: calc(100% - 20px);
              background: grey;
              color: white;
              padding: 5px 10px;
              margin: 5px;
              border-radius: 3px;
              .repTextDiv {

              }              
              .repInfoDiv {    
                width: 100%;            
                display: flex;
                flex-direction: row;
                justify-content: flex-end;
                .repNickname{}
                .repTime{

                  &:before {
                    color: blue;
                    content: "\00a0 | \00a0";
                  }
                }
                .repDate{

                  &:before {
                    color: blue;
                    content: "\00a0 | \00a0";
                  }
                }
              }
            }        
          }

          .solved {
          border: 2px green solid;
          box-shadow: 4px 4px green;
        }
        }
      }
    }
  }
  .deleteModal {
    position: absolute;
    width: 250px;
    top: -170px;
    left: -3px;
    transform: scale(0);
    transition: 200ms ease-in-out;
    border: 3px black solid;
    max-width: 300px;
    border-radius: 5px;
    background: #ffffff;
    z-index: 2;
    padding: 10px;

    .modalHead {
      display: flex;
      justify-content: space-evenly;
      padding: 10px;
      width: calc(100% - 20px);
      position: relative;

      .modalTitle {
        position: absolute;
        left: 0;
        top: 0;
        font-size: 1.3rem;
        color: black;
      }
      .modalClose {
        position: absolute;
        color: red;
        font-size: 1.3rem;
        border: none;
        font-weight: 900;
        margin: 0;
        right: 0;
        top: 0;
      }
    }
    .modalPara {
      margin-top: 20px;
      margin-bottom: 20px;
    }
    .deleteBtnModal {
      width: 100%;
      border: none;
      border: #333 solid 1px;
      background: red;
      color: black;
      font-weight: 600;
      font-size: 1rem;
      padding: 5px;
      text-align: center;
      cursor: pointer;
    }
  }

  .deleteModal.active {
    transform: scale(1);
  }

  @keyframes fadein {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes fadeout {
    from {
      opacity: 1;
    }
    to {
      opacity: 0;
    }
  }

  .replyForm {
    background: darkgrey;
    padding: 20px 25px;
    border-radius: 3px;
    margin-top: 15px;
    display: none;
    position: relative;
    transition: 0.25s all ease-in;
    z-index: 2;

    .repForm {
      display: flex;
      flex-direction: column;
      width: 100%;
      .repTextInput {
        margin-bottom: 10px;
        resize: none;
        height: 100px;
        padding: 8px;
        font-size: 0.9rem;
      }
      .repSubmit {
        width: 100px;
        align-self: center;
        padding: 5px 10px;
        cursor: pointer;
      }
    }
    .repClose {
      position: absolute;
      top: 0;
      right: 0;
      background: red;
      font-weight: 600;
      font-size: 1rem;
      border: none;
      border-bottom-left-radius: 5px;
    }
  }

  .replyForm.active {
    animation: fadein 0.5s;
    display: flex;
  }

  #overlay {
    position: fixed;
    opacity: 0;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    pointer-events: none;
    transition: 200ms ease-in-out;
    z-index: 2;
  }
  #overlay.active {
    opacity: 1;
    pointer-events: all;
  }

  .singleMain {
    padding: 50px 25px 50px 25px;
    .titleTextSection {
      .singleTitle {
      }
      .singleText {
      }
    }
  }
}
